.side-menu {
  background: #effbff;
  padding-left: 12px;
  padding-top: 24px;
  height: 100%; }
  @media screen and (max-width: 47.9375em) {
    .side-menu {
      display: none; } }
  .side-menu__title {
    text-align: left;
    font-size: 1.3125rem;
    margin-bottom: 3px; }
  .side-menu__list {
    margin: 0;
    padding: 0;
    margin-bottom: 16px;
    list-style: none; }
    .side-menu__list li {
      margin-bottom: 4px; }
      .side-menu__list li:last-child {
        margin-bottom: 0; }
      .side-menu__list li::after {
        display: table;
        content: "";
        clear: both; }
      .side-menu__list li.current a {
        color: #B3B3B3; }
    .side-menu__list a {
      text-decoration: none; }
  .side-menu__count {
    min-width: 25px;
    float: right;
    padding: 6px 3px;
    line-height: 0.9;
    text-align: center;
    margin: 0 8px;
    background: #00baa2;
    color: #fff;
    border-radius: 3px; }
  .side-menu__item-icon {
    color: #a6a6a6;
    margin-right: 10px; }
    .side-menu__item-icon--online {
      color: #00ad97; }
